.footer-container {
    background-color: var(--blue-text-color);
    font-weight: 700;
    padding-top: 15px;
    padding-bottom: 30px;
    text-decoration: none;

    @include tablet {
        padding: 50px 0;
        display: flex;
    }

    @include desktop {
        padding: 45px 0;
    }
}

.menu-link {
    text-decoration: none;
    color: var(--blck-text-color);
}

.menu-mob {
    font-size: 16px;
    line-height: 22px;

    @include tablet {
        display: none;
    }
}

.logo-header {
    font-size: 24px;
    line-height: 33px;

    @include tablet {
        font-size: 32px;
        line-height: 44px;
    }

    @include desktop {
        font-size: 42px;
        line-height: 57px;
    }
}

.menu-list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    list-style: none;

    @include tablet {
        gap: 70px;
    }

    @include desktop {
        gap: 68px;
    }
}

.menu-item {
    display: none;
    text-decoration: none;

    @include tablet {
        display: flex;
        font-size: 16px;
        line-height: 22px;
    }

    @include desktop {
        display: flex;
        font-size: 18px;
        line-height: 25px;
    }
}

.team-name {
    color: var(--white-text-color);
    margin-top: 10px;
}